import { Grid2, Pagination, Stack, Typography } from '@mui/material';
import React, { useState } from 'react';
import Sidebar from '../components/Sidebar';
import TableItem from '../components/TableItem';

const Schedule = () => {
  const [data, setData] = useState([
    {
      id: 1,
      data: [
        { label: ' ', value: '001' },
        { label: ' ', value: '' },
        { label: '', value: '  ' },
        { label: '', value: '123/2024' },
        { label: ' ', value: '2024-11-10' },
        { label: ' ', value: '' },
        { label: '', value: '' },
      ],
    },
    {
      id: 2,
      data: [
        { label: ' ', value: '002' },
        { label: ' ', value: '' },
        { label: '', value: '  ' },
        { label: '', value: '124/2024' },
        { label: ' ', value: '2024-11-12' },
        { label: ' ', value: '' },
        { label: '', value: ' ' },
      ],
    },
    {
      id: 3,
      data: [
        { label: ' ', value: '003' },
        { label: ' ', value: '' },
        { label: '', value: '' },
        { label: '', value: '125/2024' },
        { label: ' ', value: '2024-11-14' },
        { label: ' ', value: '' },
        { label: '', value: '' },
      ],
    },
    //   27    
    ...Array.from({ length: 27 }, (_, index) => ({
      id: index + 4,
      data: [
        { label: ' ', value: `00${index + 4}` },
        {
          label: ' ',
          value: index % 2 === 0 ? '' : '',
        },
        { label: '', value: ' ' },
        { label: '', value: `12${index + 4}/2024` },
        { label: ' ', value: `2024-11-${15 + (index % 15)}` },
        {
          label: ' ',
          value:
            index % 3 === 0
              ? ''
              : index % 3 === 1
              ? ''
              : '',
        },
        { label: '', value: index % 2 === 0 ? ' ' : '' },
      ],
    })),
  ]);

  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  const handleChangePage = (event, page) => {
    setCurrentPage(page);
  };

  const paginatedData = data.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const labels = paginatedData[0]?.data.map((item) => item.label) || [];
  const rows = paginatedData.map((wagon) => wagon.data);

  const handleStatusChange = (id, newStatus) => {
    setData((prevData) =>
      prevData.map((item) =>
        item.id === id
          ? {
              ...item,
              data: item.data.map((field) =>
                field.label === ''
                  ? { ...field, value: newStatus }
                  : field
              ),
            }
          : item
      )
    );
  };

  return (
    <Grid2 container>
      <Grid2 size={2}>
        <Sidebar />
      </Grid2>
      <Grid2 size={10}>
        <div className='content'>
          <Typography variant='h4' sx={{ mb: 2 }}>
            
          </Typography>
          <TableItem
            rows={rows}
            labels={labels}
            onStatusChange={handleStatusChange}
          />
          <Stack sx={{ justifyContent: 'center', mt: 2 }}>
            <Pagination
              count={Math.ceil(data.length / itemsPerPage)}
              page={currentPage}
              onChange={handleChangePage}
              shape='rounded'
              sx={{ width: '100%' }}
            />
          </Stack>
        </div>
      </Grid2>
    </Grid2>
  );
};

export default Schedule;
