import { Grid2, Pagination, Stack, Typography, CircularProgress } from '@mui/material';
import React, { useState, useEffect } from 'react';
import Sidebar from '../components/Sidebar';
import TableItem from '../components/TableItem';
import ModalShowInfo from '../components/ModalShowInfo';
import { SERVER } from '../const';


//       API
const fetchWagons = async () => {
  const token = localStorage.getItem('authToken');

  try {
    const response = await fetch(SERVER + '/calculations', {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${token}`, //  
      },
    });

    if (response.status === 401) {
      //     
      console.error('Unauthorized, redirecting to login');
      window.location.href = '/template/#/auth/login'; //    
      return; //   
    }

    if (!response.ok) {
      throw new Error('     ');
    }

    return await response.json();
  } catch (error) {
    console.error('   :', error);
    throw error; //   
  }
};


//    
const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString('ru-RU'); //    ".."
};

const History = () => {
  const [data, setData] = useState([]);          //    
  const [selectedData, setSelectedData] = useState(null);  //    
  const [currentPage, setCurrentPage] = useState(1); //  
  const [loading, setLoading] = useState(true);    //  
  const [error, setError] = useState(null);        //    
  const itemsPerPage = 10; //    

  //    API   
  useEffect(() => {
    const getData = async () => {
      setLoading(true);  //   
      setError(null);    //  
      try {
        const wagonsData = await fetchWagons();
        setData(wagonsData);  //     
      } catch (err) {
        setError('   ');  //  
      } finally {
        setLoading(false);  //  
      }
    };

    getData();
  }, []);

  //    
const handleRowClick = (rowData) => {
  //      filteredData
  const selectedRow = rows.find(row => row.some(item => item.value === rowData[0].value)); //    , ,   
  setSelectedData(selectedRow); //    rows  selectedData
};


  //   
  const handleCloseModal = () => {
    setSelectedData(null);
  };

  //   
  const handleChangePage = (event, page) => {
    setCurrentPage(page);
  };

  //  
  const paginatedData = data.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  //    .   ,   .
  const labels = [
    ' ',
    ' ',
    '',
    ' ',
    ' ',
    ' ',
    '',
    '  ',
  ];

  //        
  const filteredData = paginatedData.map((wagon) => {
    return [
      { label: 'id', value: wagon.id },  // id  
      { label: ' ', value: wagon.wagon_number },
      { label: ' ', value: wagon.wagon_type },
      { label: '', value: wagon.customer },
      { label: ' ', value: formatDate(wagon.start_repair) }, //  
      { label: ' ', value: formatDate(wagon.end_repair) }, //  
      { label: ' ', value: wagon.repair_type },
      { label: '', value: wagon.total },
      { label: '  ', value: wagon.total_with_vat },
    ];
  });

  //     ,   
  const rows = paginatedData.map((wagon) => {
    return [
      { label: 'id', value: wagon.id },  // id  
      { label: ' ', value: wagon.wagon_number },
      { label: ' ', value: wagon.wagon_type },
      { label: '', value: wagon.customer },
      { label: ' ', value: formatDate(wagon.start_repair) }, //  
      { label: ' ', value: formatDate(wagon.end_repair) }, //  
      { label: ' ', value: wagon.repair_type },
      { label: ' ', value: wagon.work_group.join(', ') },  //     
      { label: ' ', value: wagon.work_cost },
      { label: ' ', value: wagon.material_cost },
      { label: '', value: wagon.energy_cost },
      { label: '', value: wagon.fuel_cost },
      { label: ' ', value: wagon.social_contributions },
      { label: '', value: wagon.total },
      { label: '  ', value: wagon.total_with_vat },
      
    ];
  });

  return (
    <Grid2 container>
      <Grid2 size={2}>
        <Sidebar />
      </Grid2>
      <Grid2 size={10}>
        <div className="content">
          <Typography variant="h4" sx={{ mb: 2 }}>
            
          </Typography>

          {/*   */}
          {loading && <CircularProgress />}

          {/*    */}
          {error && <Typography color="error">{error}</Typography>}

          {/*  ,    */}
          {filteredData.length > 0 && !loading && !error ? (
            <TableItem
              rows={filteredData}  //   
              labels={labels}
              onRowClick={handleRowClick}
            />
          ) : (
            !loading && !error && <Typography>   </Typography>
          )}

          <Stack sx={{ justifyContent: 'center', mt: 2 }}>
            <Pagination
              count={Math.ceil(data.length / itemsPerPage)}
              page={currentPage}
              onChange={handleChangePage}
              shape="rounded"
              sx={{ width: '100%' }}
            />
          </Stack>

          {/*      */}
          <ModalShowInfo
            open={!!selectedData}
            handleClose={handleCloseModal}
            data={selectedData || []}  //      
          />
        </div>
      </Grid2>
    </Grid2>
  );
};

export default History;
