import React, { useState } from 'react';
import { Modal, Box, Button, TextField, Typography, IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import CloseIcon from '@mui/icons-material/Close'; //    "X"
import styles from './accountModal.module.css'; //     
import { SERVER } from '../const';

const AccountModal = ({ open, handleClose }) => {
  const navigate = useNavigate();

  const [oldPassword, setOldPassword] = useState('');
const [newPassword, setNewPassword] = useState('');
const [confirmPassword, setConfirmPassword] = useState('');
const [error, setError] = useState(''); //    

const handleSubmit = async () => {
  if (newPassword === confirmPassword) {
    try {
      //    localStorage
      const token = localStorage.getItem('authToken');

      if (!token) {
        setError('  , ,   ');
        return;
      }

      //       
      const response = await fetch(SERVER+'/auth/changepassword', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify({
          currentPassword: oldPassword,  //  currentPassword,   
          newPassword,
        }),
      });

      const data = await response.json();

      if (response.ok) {
        //    
        alert('  !');
        handleClose(); //   
      } else {
        //    
        setError(data.message || '    ');
      }
    } catch (error) {
      console.error(error);
      setError('     ');
    }
  } else {
    setError('  !');
  }
};


  const handleLogout = () => {
    //    localStorage  
    localStorage.removeItem('authToken');
    
    //     (,    )
    console.log('  ');
    
    // ,    :
    navigate('/auth/login');
    
    handleClose(); //     
  };

  return (
    <Modal open={open} onClose={handleClose} className={styles.modal}>
      <Box className={styles.modalContent}>
        {/*     */}
        <IconButton
          edge="end"
          color="inherit"
          onClick={handleClose}
          className={styles.closeButton}
        >
          <CloseIcon />
        </IconButton>

        <Typography variant="h6" gutterBottom>
           
        </Typography>

        {/*  */}
        {error && <Typography color="error">{error}</Typography>}

        {/*       */}
        <TextField
          label=" "
          type="password"
          fullWidth
          variant="outlined"
          margin="normal"
          value={oldPassword}
          onChange={(e) => setOldPassword(e.target.value)}
        />
        <TextField
          label=" "
          type="password"
          fullWidth
          variant="outlined"
          margin="normal"
          value={newPassword}
          onChange={(e) => setNewPassword(e.target.value)}
        />
        <TextField
          label="  "
          type="password"
          fullWidth
          variant="outlined"
          margin="normal"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
        />

        {/*  */}
        <div className={styles.buttonContainer}>
          <Button variant="contained" color="primary" onClick={handleSubmit}>
             
          </Button>
          <Button variant="outlined" color="primary" onClick={handleLogout}>
            
          </Button>
        </div>
      </Box>
    </Modal>
  );
};

export default AccountModal;
