import { Grid2, Stack, Typography, Snackbar, Alert } from '@mui/material';
import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom'; //   useLocation
import Sidebar from '../components/Sidebar';
import TextFieldItem from '../components/TextField';
import ButtonItem from '../components/Button';
import SelectForm from '../components/Select';
import { SERVER } from '../const';

const labels = [
  { key: 'wagonNumber', label: ' ', type: 'text' },
  { key: 'wagonType', label: ' ', type: 'select' },
  { key: 'customer', label: '', type: 'text' },
  { key: 'repairStart', label: ' ', type: 'date' },
  { key: 'repairEnd', label: ' ', type: 'date' },
  { key: 'repairType', label: ' ', type: 'select' },
  { key: 'workGroup', label: ' ', type: 'select' },
  { key: 'workName', label: ' ', type: 'text' },
  { key: 'workCost', label: ' ', type: 'number' },
  { key: 'materials', label: ' ', type: 'select' },
  { key: 'materialCost', label: ' ', type: 'number' },
  { key: 'electricityCost', label: ' ()', type: 'number' },
  { key: 'fuelCost', label: ' ()', type: 'number' },
  {
    key: 'socialContributions',
    label: '  ()',
    type: 'number',
  },
];

const options = {
  wagonType: [
    { label: '', value: '' },
    { label: '', value: '' },
    { label: '', value: '' },
    { label: '', value: '' },
  ],
  repairType: [
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
  ],
  workGroup: [
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: '  ', value: '  ' },
    { label: ' ', value: ' ' },
    {
      label: '    ',
      value: '    ',
    },
    { label: ' ', value: ' ' },
    {
      label: '    ',
      value: '    ',
    },
    { label: '  ', value: '  ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    {
      label: '   ',
      value: '   ',
    },
    { label: '  ', value: '  ' },
  ],
  materials: [
    { label: '', value: '' },
    { label: '', value: '' },
    { label: '', value: '' },
    { label: '', value: '' },
  ],
  executor: [
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
  ],
};

const Calculation = () => {
  const [fields, setFields] = useState({});
  const [total, setTotal] = useState(0);
  const [totalWithVAT, setTotalWithVAT] = useState(0);
  const [snackbarOpen, setSnackbarOpen] = useState(false); // State  Snackbar
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('success');

  //     useLocation
  const location = useLocation();
  const { state } = location;

  console.log('######################################', state?.data); //  

  //   ,    
  useEffect(() => {
    if (state && state.data) {
      const initialFields = {};

      //    ,   -  label,   -  value
      state.data.forEach((item) => {
        initialFields[item.label] = item.value;
      });

      setFields(initialFields);
    }
  }, [state]);

  const handleFieldChange = (key, value) => {
    setFields((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const calculateTotal = () => {
    const workCost = parseFloat(fields.workCost || 0);
    const materialCost = parseFloat(fields.materialCost || 0);
    const electricityCost = parseFloat(fields.electricityCost || 0);
    const fuelCost = parseFloat(fields.fuelCost || 0);
    const socialContributions = parseFloat(fields.socialContributions || 0);

    const totalAmount =
      workCost +
      materialCost +
      electricityCost +
      fuelCost +
      socialContributions;

    setTotal(totalAmount);
    setTotalWithVAT(totalAmount * 1.12); // 12% 
  };
  const addTotalToFields = (fields) => {
    const workCost = parseFloat(fields.workCost || 0);
    const materialCost = parseFloat(fields.materialCost || 0);
    const electricityCost = parseFloat(fields.electricityCost || 0);
    const fuelCost = parseFloat(fields.fuelCost || 0);
    const socialContributions = parseFloat(fields.socialContributions || 0);

    //       
    const totalAmount =
      workCost +
      materialCost +
      electricityCost +
      fuelCost +
      socialContributions;

    const totalWithVAT = totalAmount * 1.12; // 12% 

    //      
    return {
      ...fields,
      total: totalAmount,
      totalWithVAT: totalWithVAT,
    };
  };

  const handleFieldsRender = ({ key, label, type }) => {
    //   ,    ,    
    const fieldValue = fields[label] || ''; //   ,  

    if (key === 'workCost' || key === 'materialCost') return;

    if (key === 'workGroup') {
      return (
        <SelectForm
          label={label}
          value={Array.isArray(fields[label]) ? fields[label] : []} // ,    
          options={options[key]}
          handleChange={(value) => handleFieldChange(label, value)} // handleChange    
          multiple //     
          size='small'
          sx={{ mb: 3 }}
        />
      );
    }

    if (key === 'workName') {
      return (
        <Grid2 container spacing={1} sx={{ mb: 3 }} key={label}>
          <Grid2 size={7}>
            <TextFieldItem
              label={label} //  
              value={fields[key] || ''} //    
              handleChange={(e) => handleFieldChange(key, e.target.value)} //  
              size='small'
              fullWidth
            />
          </Grid2>
          <Grid2 size={5}>
            <TextFieldItem
              label=' '
              value={fields['workCost'] || ''} //   workCost
              handleChange={(e) =>
                handleFieldChange('workCost', e.target.value)
              } //    workCost
              size='small'
              type='number'
              fullWidth
            />
          </Grid2>
        </Grid2>
      );
    }

    if (key === 'materials') {
      return (
        <Grid2 container spacing={1} sx={{ mb: 3 }} key={label}>
          <Grid2 size={7}>
            <SelectForm
              label={label}
              value={fieldValue}
              options={options[key]}
              handleChange={(value) => handleFieldChange(label, value)}
              size='small'
            />
          </Grid2>
          <Grid2 size={5}>
            <TextFieldItem
              label=' '
              value={fields['materialCost'] || ''} //   ,   
              handleChange={(e) =>
                handleFieldChange('materialCost', e.target.value)
              }
              size='small'
              type='number'
              fullWidth
            />
          </Grid2>
        </Grid2>
      );
    }
    const isMultipleSelect = key === 'workGroup'; //    
    switch (type) {
      case 'select':
        return (
          <SelectForm
            label={label}
            value={fields[label] || (isMultipleSelect ? [] : '')} // ,  value    multiple
            options={options[key]}
            handleChange={(value) => handleFieldChange(label, value)}
            multiple={isMultipleSelect} //  multiple    
            size='small'
            sx={{ mb: 3 }}
          />
        );
      case 'date':
        //   'date'     yyyy-MM-dd
        const formattedDate = fieldValue
          ? new Date(fieldValue).toISOString().split('T')[0]
          : '';
        return (
          <TextFieldItem
            key={key}
            label={label}
            value={formattedDate} //  
            handleChange={(e) => handleFieldChange(label, e.target.value)}
            fullWidth
            size='small'
            sx={{ mb: 3 }}
            type='date'
            focused
          />
        );
      case 'array':
        //   array (,  " "),    ,   
        return (
          <TextFieldItem
            key={key}
            label={label}
            value={Array.isArray(fieldValue) ? fieldValue.join(', ') : ''} //    
            handleChange={(e) => {
              const updatedArray = e.target.value
                .split(',')
                .map((item) => item.trim());
              handleFieldChange(label, updatedArray); //   
            }}
            fullWidth
            size='small'
            sx={{ mb: 3 }}
            type='text'
          />
        );
      default:
        return (
          <TextFieldItem
            key={key}
            label={label}
            value={fieldValue}
            handleChange={(e) => handleFieldChange(label, e.target.value)}
            fullWidth
            size='small'
            sx={{ mb: 3 }}
            type={type}
          />
        );
    }
  };
  const handleSave = async () => {
    const token = localStorage.getItem('authToken');
  
    try {
      //  total  totalWithVAT  fields
      const updatedFields = addTotalToFields(fields);
  
      //     
      const response = await fetch(SERVER + '/calculations', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`, //  
        },
        body: JSON.stringify(updatedFields), //   
      });
  
      console.log(JSON.stringify(updatedFields)); //  
  
      if (response.status === 401) {
        //     
        console.error('Unauthorized, redirecting to login');
        window.location.href = '/template/#/auth/login'; //    
        return; //   
      }
  
      if (!response.ok) {
        setSnackbarMessage(response.status);
        setSnackbarSeverity('error');
      } else {
        setSnackbarMessage('  !');
        setSnackbarSeverity('success');
      }
    } catch (error) {
      setSnackbarMessage('    .');
      setSnackbarSeverity('error');
    }
  
    setSnackbarOpen(true);
  };
  

  return (
    <Grid2 container>
      <Grid2 size={2}>
        <Sidebar />
      </Grid2>
      <Grid2 size={10}>
        <Snackbar
          open={snackbarOpen}
          autoHideDuration={3000}
          onClose={() => setSnackbarOpen(false)}
        >
          <Alert
            onClose={() => setSnackbarOpen(false)}
            severity={snackbarSeverity}
            sx={{ width: '100%' }}
          >
            {snackbarMessage}
          </Alert>
        </Snackbar>
        <div className='content'>
          <Typography variant='h4' sx={{ mb: 2 }}>
            
          </Typography>
          {labels.map(handleFieldsRender)}
          <ButtonItem
            label=''
            variant='outlined'
            size='small'
            handleChange={calculateTotal}
            sx={{ mb: 2 }}
          />
          <ButtonItem
            label=''
            variant='outlined'
            size='small'
            onClick={handleSave}
          />
          <Typography variant='h6'>
             ( ): {total.toFixed(2)} 
          </Typography>
          <Typography variant='h6'>
             ( ): {totalWithVAT.toFixed(2)} 
          </Typography>
        </div>
      </Grid2>
    </Grid2>
  );
};

export default Calculation;
