import React, { useState } from 'react';
import Sidebar from '../components/Sidebar';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid2,
  Typography,
} from '@mui/material';
import InventoryCard from '../components/InventoryCard';
import ModalInventoryList from '../components/ModalInventoryList';
import ModalAddItem from '../components/ModalAddItem';
import ButtonItem from '../components/Button';
import TrainCard from '../components/TrainCard';
import ModalAddTrain from '../components/ModalAddTrain';
import TextFieldItem from '../components/TextField';
import SelectForm from '../components/Select';
import { SERVER } from '../const';

const labels = [
  { key: 'wagonnumber', label: ' ', type: 'text' },
  { key: 'wagontype', label: ' ', type: 'select' },
  { key: 'customer', label: '', type: 'text' },
  { key: 'contract', label: '', type: 'text' },
  { key: 'repairstart', label: ' ', type: 'date' },
  { key: 'repairend', label: ' ', type: 'date' },
  { key: 'repairtype', label: ' ', type: 'select' },
  { key: 'workgroup', label: ' ', type: 'select' },
  { key: 'workname', label: ' ', type: 'text' },
  { key: 'executor', label: '', type: 'select' },
];

const options = {
  wagontype: [
    { label: '', value: '' },
    { label: '', value: '' },
    { label: '', value: '' },
    { label: '', value: '' },
  ],
  repairtype: [
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
  ],
  workgroup: [
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: '  ', value: '  ' },
    { label: ' ', value: ' ' },
    {
      label: '    ',
      value: '    ',
    },
    { label: ' ', value: ' ' },
    {
      label: '    ',
      value: '    ',
    },
    { label: '  ', value: '  ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    {
      label: '   ',
      value: '   ',
    },
    { label: '  ', value: '  ' },
  ],
  executor: [
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
  ],
};

const Trains = () => {
  const [fields, setFields] = useState({});

  const handleFieldChange = (key, value) => {
    setFields((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const handleFieldsRender = ({ key, label, type }) => {
    const isMultipleSelect = key === 'workgroup'; //    
    switch (type) {
      case 'select':
        return (
          <SelectForm
            label={label}
            value={fields[label] || (isMultipleSelect ? [] : '')} // ,  value    multiple
            options={options[key]}
            handleChange={(value) => handleFieldChange(label, value)}
            multiple={isMultipleSelect} //  multiple    
            size='small'
            sx={{ mb: 3 }}
          />
        );
      case 'date':
        return (
          <TextFieldItem
            key={key}
            label={label}
            value={fields[label]}
            handleChange={(e) => handleFieldChange(label, e.target.value)}
            fullWidth
            size='small'
            sx={{ mb: 3 }}
            type='date'
            focused
          />
        );
      default:
        return (
          <TextFieldItem
            key={key}
            label={label}
            value={fields[label]}
            handleChange={(e) => handleFieldChange(label, e.target.value)}
            fullWidth
            size='small'
            sx={{ mb: 3 }}
          />
        );
    }
  };

  const mapDataToKeys = (data) => {
    const mappedData = {};

    labels.forEach((label) => {
      const key = label.key;
      const labelName = label.label;

      for (const [k, value] of Object.entries(data)) {
        if (labelName === k) {
          mappedData[key] = value;
        }
      }
    });

    return mappedData;
  };

  const handleAddWagon = async () => {
    const token = localStorage.getItem('authToken');
  
    try {
      const data = mapDataToKeys(fields);
      data['workgroup'] = Array.isArray(data['workgroup'])
        ? data['workgroup']
        : [data['workgroup']];
  
      const response = await fetch(SERVER + '/trains', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`, //  
        },
        body: JSON.stringify(data),
      });
  
      if (response.status === 401) {
        //     
        console.error('Unauthorized, redirecting to login');
        window.location.href = '/template/#/auth/login'; //    
        return; //   
      }
  
      if (response.ok) {
        const result = await response.json();
        console.log(result);
        alert('  !');
        setFields({}); //     
      } else {
        const error = await response.text();
        alert(`: ${error}`);
      }
    } catch (error) {
      console.error('   :', error);
      alert('    .');
    }
  };
  

  return (
    <Grid2 container>
      <Grid2 size={2}>
        <Sidebar />
      </Grid2>
      <Grid2 size={10}>
        <div className='content'>
          <Typography variant='h4' sx={{ mb: 2 }}>
            
          </Typography>
          {labels.map(handleFieldsRender)}
          <ButtonItem
            label=''
            variant='outlined'
            size='small'
            handleChange={handleAddWagon}
            sx={{ mb: 2 }}
          />
        </div>
      </Grid2>
    </Grid2>
  );
};

export default Trains;
