import { useNavigate } from 'react-router-dom';
import formatDate from '../utils/formatDate';
import ButtonItem from './Button'; //   
import ButtonFile from './ButtonFile';
import ModalItem from './ModalItem';
import TextFieldItem from './TextField';
import { Stack } from '@mui/material';
import { useState,useEffect } from 'react';
import { SERVER } from '../const';

const ModalMain = ({
  open,
  handleClose,
  id,
  deleteTrain,
  handleWorkGroupStatusChange,
  data = [],
}) => {
  const navigate = useNavigate(); //   
  const [currentWorkGroup, setCurrentWorkGroup] = useState('');
  const [showOptions, setShowOptions] = useState(false);
  const [fileExists, setFileExists] = useState(false); //      
  const [fileName, setFileName] = useState(''); //     


  // ,     ""
  const isReady = data.some(
    (row) => row.label === '' && row.value === ''
  );

   //   
useEffect(() => {
  const wagonNumber = data.find(row => row.label === ' ')?.value;
  if (wagonNumber) {
    const token = localStorage.getItem('authToken');
    
    fetch(`${SERVER}/files/${wagonNumber}/exist`, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${token}`,
      },
    })
      .then(res => {
        if (res.status === 401) {
          //     
          console.error('Unauthorized, redirecting to login');
          window.location.href = '/template/#/auth/login'; //    
          return; //   
        }
        return res.json();
      })
      .then(data => {
        if (data) {
          console.log(data.exists);
          setFileExists(data.exists); //  ,   
          if (data.exists) {
            setFileName(data.message); //   
          }
        }
      })
      .catch(console.error);
  }
}, [data]);

  


  const handleCalculationClick = () => {
    //     state  
    navigate('/calculation', { state: { data } });
  };

  const handleSelectWorkgroup = (group) => {
    setCurrentWorkGroup(group);
    setShowOptions(true);
  };

  const calcVariant = (val) => {
    const statuses = data.find(
      (row) => row.label === '  '
    ).value;
    if (statuses !== ' ') {
      const status = statuses.find((row) => row.value === val);
      if (status && status.status === '') {
        return 'success';
      }
      return 'primary';
    }
  };
   //    
   const handleFileUpload = (event) => {
    const formData = new FormData();
    formData.append('file', event.target.files[0]); //  
    const wagonNumber = data.find(row => row.label === ' ')?.value;
    formData.append('wagonNumber', wagonNumber);
  
    const token = localStorage.getItem('authToken');
  
    fetch(`${SERVER}/files/upload`, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${token}`,
      },
      body: formData,
    })
      .then(res => {
        if (res.status === 401) {
          //     
          console.error('Unauthorized, redirecting to login');
          window.location.href = '/template/#/auth/login'; //    
          return; //   
        }
        return res.json();
      })
      .then(data => {
        if (data) {
          alert('  ');
          setFileExists(true);
          setFileName(data.file.filename); //     
        }
      })
      .catch(console.error);
  };
  

  //    
const handleDownloadFile = () => {
  const wagonNumber = data.find(row => row.label === ' ')?.value;
  const token = localStorage.getItem('authToken');

  fetch(`${SERVER}/files/${wagonNumber}`, {
    method: 'GET',
    headers: {
      'Authorization': `Bearer ${token}`,
    },
  })
    .then(response => {
      if (response.status === 401) {
        //     
        console.error('Unauthorized, redirecting to login');
        window.location.href = '/template/#/auth/login'; //    
        return; //   
      }
      if (!response.ok) throw new Error("  ");
      return response.blob();
    })
    .then(blob => {
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = fileName; //     
      document.body.appendChild(a);
      a.click();
      a.remove();
    })
    .catch(console.error);
};


  //    
const handleDeleteFile = () => {
  const wagonNumber = data.find(row => row.label === ' ')?.value;
  const token = localStorage.getItem('authToken');

  fetch(`${SERVER}/files/${wagonNumber}`, {
    method: 'DELETE',
    headers: {
      'Authorization': `Bearer ${token}`,
    },
  })
    .then(res => {
      if (res.status === 401) {
        //     
        console.error('Unauthorized, redirecting to login');
        window.location.href = '/template/#/auth/login'; //    
        return; //   
      }
      return res.json();
    })
    .then(data => {
      if (data) {
        alert('  ');
        setFileExists(false); //    
        setFileName(''); //   
      }
    })
    .catch(console.error);
};


  return (
    <ModalItem open={open} handleClose={handleClose} title=''>
      {data.map((row, index) => {
        if (row.label !== '  ') {
          if (row.label === ' ') {
            if (Array.isArray(row.value)) {
              return (
                <div>
                  <Stack direction='row' spacing={1} mb={2}>
                    {row.value.map((val) => (
                      <div>
                        <ButtonItem
                          size='small'
                          handleChange={() => handleSelectWorkgroup(val)}
                          label={val}
                          color={calcVariant(val)}
                          variant='outlined'
                          sx={{ mb: 1 }}
                        />
                        {showOptions && currentWorkGroup === val && (
                          <Stack direction='row' spacing={1}>
                            <ButtonItem
                              size='small'
                              variant='outlined'
                              handleChange={() =>
                                handleWorkGroupStatusChange(
                                  ' ',
                                  currentWorkGroup
                                )
                              }
                              label=' '
                            />
                            <ButtonItem
                              size='small'
                              variant='outlined'
                              handleChange={() =>
                                handleWorkGroupStatusChange(
                                  '',
                                  currentWorkGroup
                                )
                              }
                              label=''
                            />
                          </Stack>
                        )}
                      </div>
                    ))}
                  </Stack>
                </div>
              );
            } else {
              return (
                <div>
                  <Stack direction='row' spacing={1} mb={1}>
                    <ButtonItem
                      size='small'
                      variant='outlined'
                      handleChange={() => handleSelectWorkgroup(row.value)}
                      label={row.value}
                      color={calcVariant(row.value)}
                    />
                  </Stack>
                  {showOptions && (
                    <Stack direction='row' spacing={1} mb={2}>
                      <ButtonItem
                        size='small'
                        variant='outlined'
                        handleChange={() =>
                          handleWorkGroupStatusChange(
                            ' ',
                            currentWorkGroup
                          )
                        }
                        label=' '
                      />
                      <ButtonItem
                        size='small'
                        variant='outlined'
                        handleChange={() =>
                          handleWorkGroupStatusChange(
                            '',
                            currentWorkGroup
                          )
                        }
                        label=''
                      />
                    </Stack>
                  )}
                </div>
              );
            }
          }
          const value = row.value; //  ,   
          return (
            <TextFieldItem
              key={index}
              label={row.label}
              value={
                row.label === ' ' ||
                row.label === ' ' ||
                row.label === ''
                  ? formatDate(value)
                  : value
              }
              handleChange={() => {}}
              fullWidth
              size='small'
              sx={{ mb: 2 }}
              disabled
            />
          );
        }
      })}
      <div
  style={{
    display: 'flex',
    justifyContent: 'space-between',
    marginTop: 'auto',
  }}
>
  {/*   */}
  <div style={{ display: 'flex', justifyContent: 'space-between' }}>
    {/*  "" () */}
    <ButtonItem
      label=""
      variant="contained"
      color="error"
      handleChange={() => deleteTrain(id)}
      sx={{
        mt: 2,
        backgroundColor: '#ff2441',
        color: 'white',
        '&:hover': { backgroundColor: 'darkred' },
      }}
    />
    {/*  "" (),     "" */}
    {isReady && (
      <ButtonItem
        label=""
        variant="contained"
        color="success"
        handleChange={handleCalculationClick} //        
        sx={{
          mt: 2,
          backgroundColor: 'green',
          color: 'white',
          '&:hover': { backgroundColor: 'darkgreen' },
        }}
      />
    )}
  </div>

  {/*   */}
  <div style={{ display: 'flex', justifyContent: 'space-between' }}>
    {/*     (),      */}
    {!fileExists && (
      <ButtonFile
        label=" "
        handleChange={handleFileUpload} //   
        variant="contained"
        color="primary"
        sx={{
          mt: 2,
          backgroundColor: '#2196f3',
          color: 'white',
          '&:hover': { backgroundColor: '#1976d2' },
        }}
      />
    )}

    {/*  " " (),     */}
    {fileExists && (
      <ButtonItem
        label=" "
        variant="contained"
        color="primary"
        handleChange={handleDownloadFile} //  
        sx={{
          mt: 2,
          backgroundColor: '#2196f3',
          color: 'white',
          '&:hover': { backgroundColor: '#1976d2' },
        }}
      />
    )}

    {/*  " " (),     */}
    {fileExists && (
      <ButtonItem
        label=" "
        variant="contained"
        color="primary"
        handleChange={handleDeleteFile} //  
        sx={{
          mt: 2,
          backgroundColor: '#2196f3',
          color: 'white',
          '&:hover': { backgroundColor: '#1976d2' },
        }}
      />
    )}
  </div>
</div>

    </ModalItem>
  );
};

export default ModalMain;